.MainBlock {
min-height: 100%;
background-color:#1f1f20;
display:flex;
justify-content: center;

}

.container {
    /* border: 1px solid;  */
    width:  80%;
    display:flex;
    justify-content: center;
    flex-direction: column;

    
}
.block {
    justify-content: space-around;
    display: flex;
    /* border: 1px solid; */
    flex-wrap: wrap;
    padding: 100px;
}

.greeting {
    
    /* border: 1px solid; */
    display: flex;
    flex-direction: column;
    align-self: center;
    color:white;
    /* z-index:1; */
}
.greeting span:nth-child(1) {
    font-size: 16px;
    /* font-weight: 800; */
    
}



 .greeting span:nth-child(2),
 .greeting span:nth-child(2) span{
    font-weight: 500;
    font-stretch: normal;
    font-size: 40px;
    line-height: 1.2;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 5px;
    position: relative;
    z-index: 0;
 
} 
.greeting span:nth-child(2) span::before{
content: "";
position: absolute;
width: 258px;
height: 15px;
background-color: #ea548d;
bottom: 0px;
z-index: -1;

}
.h1 {
    font-size: 22px
}
.photo {

border: 1px solid;

margin-top: 50px;
position: relative;
z-index: 1;
}

.image{
    background-color: rgb(100, 198, 237);
    background-image: url("https://i.pinimg.com/564x/62/18/52/6218526370390c19944e8fb4d4554176.jpg");
    width: 300px;
    height: 400px; 
    position: relative;
    /* z-index: 1;  */
}

.photo .image::before{
    content: "";
    position: absolute;
    height: 432px;
    width: 300px;
    border: 5px solid;
    /* border-color: #ea548d; */
    left: 16px;
    top: -20px;
    z-index: -1;
}